<template>
  <el-form ref="form" :model="form" label-width="80px" style="height:100px;margin:20px;width:100%;">
    <el-form-item label="活动名称">
      <el-input v-model="form.name"></el-input>
    </el-form-item>
    <el-form-item label="活动区域">
      <el-select v-model="form.region" placeholder="请选择活动区域">
        <el-option label="区域一" value="shanghai"></el-option>
        <el-option label="区域二" value="beijing"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="活动时间">
      <el-col :span="11">
        <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-time-picker
          type="fixed-time"
          placeholder="选择时间"
          v-model="form.date2"
          style="width: 100%;"
        ></el-time-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="即时配送">
      <el-switch on-text off-text v-model="form.delivery"></el-switch>
    </el-form-item>
    <el-form-item label="活动性质">
      <el-checkbox-group v-model="form.type">
        <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
        <el-checkbox label="地推活动" name="type"></el-checkbox>
        <el-checkbox label="线下主题活动" name="type"></el-checkbox>
        <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
      </el-checkbox-group>
    </el-form-item>
    <el-form-item label="特殊资源">
      <el-radio-group v-model="form.resource">
        <el-radio label="线上品牌商赞助"></el-radio>
        <el-radio label="线下场地免费"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="活动形式">
      <el-input type="textarea" v-model="form.desc"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">立即创建</el-button>
      <el-button @click="quxiao">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      }
    };
  },
  props: {
    check: {
      type: Object,
      default: () => {
        return {};
      }
    },
    layerid: {
      type: String,
      default: ''
    },
    lydata: {
      type: Object,
      default: () => {
        return {};
      }
    },
    iframeData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    fn: {
      type: Function,
      default: function () {}
    }
  },
  methods: {
    onSubmit () {
      // console.log("submit!");
      // alert(JSON.stringify(this.form));
      // console.log(this.parent);
      // console.log("$parent");
      // console.log(this.$parent);
      // console.log(this.form);
      // this.$parent.$data.iframeData = JSON.parse(JSON.stringify(this.form));
      // this.$parent.$data.iframeData = Object.assign({}, this.form);
      // console.log('lydata', this.lydata);
      // console.log('lydataForm', this.form);
      this.lydata.iframeData = Object.assign(this.lydata.iframeData, this.form);
      this.$layer.msg('成功');
      this.$layer.close(this.layerid);
      // this.$parent.$emit('asd', 'kkk');
    },
    quxiao () {
      this.fn();
      this.$layer.close(this.layerid);
    }
  },
  mounted () {
    // this.form.name = this.check.a;
    console.log(this.lydata);
  },
  watch: {
    iframeData: {
      handler: function () {
        console.log('asdasd');
        this.form = this.iframeData;
      },
      deep: true,
      immediate: true
    }
  }
};
</script>

